.main-card {
  width: 80%;
  height: 40rem;
  justify-content: space-evenly;
  flex-wrap: nowrap;
  display: inline-block !important;
}

.card_body {
  text-align: center;
}

.card-image {
  width: 100%;
  height: 25rem;
  display: block !important;
}

.dictionary > .term {
  text-align: center;
  max-width: 20rem;
  margin-bottom: 3rem;
  padding: 2rem;
  border-radius: 3px;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.05), 0 5px 20px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  transition: all 100ms ease-in-out;
}

.spacing_negetive {
  margin-left: -7px !important;
}

.spacing {
  margin-left: 20px;
  margin-top: 20px;
}

.card-company {
  display: inline-block;
  background-color: aquamarine;
  padding: 10px;
  border-radius: 10px;
}
