
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    
}



.new_arrival{
    background-color: rgba(0, 0, 0, 0.74);
    background-attachment: fixed;
}

.arrival_container{
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px;
    overflow: hidden;
}

.arrival_card{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 860px;
}

.arrival_shoeBackground{
    position: relative;
    width: 50%;
    height: 475px;
    box-shadow: -15px 0 35px rgba(0, 0, 0, 0.1),
    0 -15px 35px rgba(0, 0, 0, 0.1),
    0 15px 35px rgba(0, 0, 0, 0.1);
    transition: .5s;
}


.arrival_gradient{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}

.arrival_first{
    z-index: 0;
    animation: 1s width ease;
}



.arrival_gradient{
    background-image: linear-gradient(45deg, #111, #666);
}

.arrival_logo{
    position: absolute;
    width: 100px;
    left: 20px;
    top: 20px;
}



.arrival_nike{
    position: absolute;
    top: 85px;
    left: 15px;
    font-size: 11rem;
    text-transform: uppercase;
    line-height: .9;
    color: #fff;
    opacity: .1;
}

.arrival_shoe{
    position: absolute;
    width: 140%;
    bottom: 0;
    right: 0;
    transform: rotate(-20deg);
    transition: .5s;
}

.arrival_shoe.arrival_show{
    opacity: 1;
}

.arrival_info{
    width: 50%;
    background-color: #fff;
    z-index: 1;
    padding: 50px 40px;
    box-shadow: 15px 0 35px rgba(0, 0, 0, 0.1),
    0 -15px 35px rgba(0, 0, 0, 0.1),
    0 15px 35px rgba(0, 0, 0, 0.1);
}

.arrival_shoeName{
    padding: 0 0 10px 0;
}

.arrival_shoeName div{
    display: flex;
    align-items: center;
}

.arrival_shoeName div .arrival_big{
    margin-right: 10px;
    font-size: 2rem;
    font-weight: 700;
    color: #333;
    line-height: 1;
}

.arrival_shoeName div .arrival_new{
    background-color:#0cc2aa;
    text-transform: uppercase;
    color: #fff;
    padding: 3px 10px;
    border-radius: 5px;
    transition: .5s;
}

.arrival_shoeName .arrival_small{
    font-weight: 500;
    color: #444;
    margin-top: 3px;
    text-transform: capitalize;
    font-size: 1.3rem;
}

.arrival_shoeName, .arrival_description, .arrival_color-container, .arrival_size-container{
    border-bottom: 1px solid #dadada;
}

.arrival_description{
    padding: 25px 0 15px 0;
}

.arrival_title{
    color: #3a3a3a;
    font-weight: 600;
    font-size: 1.2rem;
    text-transform: uppercase;
}

.arrival_text{
    color: #555;
    font-size: 17px;
}

.arrival_color-container{
    padding: 10px 0;
}

.arrival_colors{
    padding: 8px 0;
    display: flex;
    align-items: center;
}

.arrival_color{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    margin: 0 10px;
    border: 5px solid;
    cursor: pointer;
    transition: .5s;
}


.arrival_color[color="black"]{
    background-color: #444;
    border-color: #444;
}

.arrival_color.arrival_active{
    border-color: #fff;
    box-shadow: 0 0 10px .5px rgba(0, 0, 0, 0.2);
    transform: scale(1.1);
}

.arrival_size-container{
    padding: 10px 0;
    margin-bottom: 10px;
}

.arrival_sizes{
    padding: 14px 0;
    display: flex;
    align-items: center;
}

.arrival_size{
    width: 40px;
    height: 40px;
    border-radius: 6px;
    background-color: #eee;
    margin: 0 10px;
    text-align: center;
    line-height: 40px;
    font-size: 1.1rem;
    font-weight: 500;
    cursor: pointer;
    transition: .3s;
}

.arrival_size.arrival_active{
    background-color: var(--primary);
    color: #fff;
    transition: .5s;
}

.arrival_buy-price{
    padding-top: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.arrival_price{
    color: #333;
    display: flex;
    align-items: flex-start;
}

.arrival_price h1{
    font-size: 2.1rem;
    font-weight: 600;
    line-height: 1;
}

.arrival_price i{
    font-size: 1.4rem;
    margin-right: 1px;
}

.arrival_buy{
    padding: .7rem 1rem;
    background-color: var(--primary);
    text-decoration: none;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 500;
    font-size: 1.1rem;
    transition: .5s;
}

.arrival_buy:hover{
    transform: scale(1.1);
}

.arrival_buy i{
    margin-right: 2px;
}

