  }
  
  .w-auto-xs {
    width: auto;
  }
  
  .p-y-md {
    padding-top: 5.5rem !important;
    padding-bottom: 1.5rem !important;
  }
  
  .p-a-md {
    padding: 1.5rem !important;
  }
  
  .box-color {
    background-color: #fff;
    position: relative;
    margin-bottom: 1.5rem;
  }
  
  .box-shadow-z1 {
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1), 0 -1px 0px rgba(0, 0, 0, 0.02);
  }
  
  .text-color {
    color: rgba(0, 0, 0, 0.87);
  }
  
  .m-a {
    margin: 1rem !important;
  }
  
  .m-b {
    margin-bottom: 1rem !important;
  }
  
  .text-sm {
    font-size: 0.8rem;
  }
  
  .md-form-group {
    padding: 18px 0 24px 0;
    position: relative;
  }
  
  .contact-form {
    padding: 15px 0 20px 0;
    position: relative;
  }
  
  .md-input {
    background: transparent;
    position: relative;
    z-index: 5;
    box-shadow: none;
    border: 0;
    border-radius: 0;
    border-bottom: 1px solid rgba(160, 160, 160, 0.3);
    width: 100%;
    height: 34px;
    padding: 2px;
    color: inherit;
  }
  
  .md-input:focus {
    border-color: #0cc2aa;
    border-bottom-width: 2px;
    padding-bottom: 1px;
  }
  .md-input:focus ~ label {
    color: #0cc2aa;
    opacity: 1;
    top: 0 !important;
    font-size: 0.85em !important;
  }
  .float-label .md-input ~ label {
    top: 20px;
    font-size: 1em;
  }
  
  .md-input ~ label {
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    font-size: 0.85em;
    position: absolute;
    z-index: 0;
    opacity: 0.5;
    display: inline-block;
    top: 0px;
    left: 0;
  }
  
  textarea.md-input {
    height: 50px !important;
  }
  
  .m-b-md {
    margin-bottom: 1.5rem !important;
  }
  
  .md-check {
    cursor: pointer;
    padding-left: 1.25rem;
    margin: 0;
    display: inline-block;
    position: relative;
  }
  .md-check input {
    position: absolute;
    cursor: pointer;
    z-index: 1;
    opacity: 0;
    margin-left: -1.25rem;
  }
  
  .md-check input:checked + i:before {
    border-width: 0;
    background-color: inherit;
  }
  
  .md-check input[type="checkbox"]:checked + i:after {
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    position: absolute;
    left: 6px;
    top: 2px;
    display: table;
    width: 6px;
    height: 12px;
    border: 2px solid;
    border-top: 0;
    border-left: 0;
    content: " ";
  }
  
  .md-check > i {
    width: 0;
    height: 18px;
    line-height: 1;
    margin-left: -20px;
    margin-top: 1px;
    float: left;
    vertical-align: middle;
    margin-right: 28px;
    position: relative;
  }
  
  .md-check > i:before {
    content: "";
    position: absolute;
    width: 18px;
    height: 100%;
    border: 2px solid #9e9e9e;
    border-radius: 2px;
  }
  
  .primary {
    color: rgba(255, 255, 255, 0.87);
    background-color: #0cc2aa;
  }
  
  .btn {
    font-weight: 500;
    outline: 0 !important;
  }
  
  .p-x-md {
    padding-right: 1.5rem !important;
    padding-left: 1.5rem !important;
  }
  
  .p-y-lg {
    padding-top: 3rem !important;
