  }
  
  .text-primary {
    color: #0cc2aa !important;
  }
  
  ._600 {
    font-weight: 600;
  }
  
  /**********************************
  Styling for contact us page
  **********************************/
  
  .container {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 100px;
  }
  
  .container:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-size: cover;
    filter: blur(50px);
    z-index: -1;
  }
  .contact-box {
    max-width: 850px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #fff;
    box-shadow: 0px 0px 19px 5px rgba(0, 0, 0, 0.19);
  }
  
  .contact_us_form {
    width: 40%;
    margin-left: auto;
    margin-right: auto;
   
  }
  
  .contact_us_padding {
    padding: 40px;
  }
  
  .contact_us_btn {
    margin-left: 70px;
    margin-top: 30px;
  }
  
  .form_text_area {
    width: 400px;
  }
  
  
  .contact_h2{
    margin-left: 100px;
    margin-bottom: 50px;
  }
  
  
  .contact_form{
    padding: 9px 0 2px 0;
  }
  
